*{
    margin: 0;
    padding: 0;
    font-family: 'Inter', sans-serif;
    ;

}

.App{
    width: 320px;
    border: 1px solid;
    margin: auto;
    border-radius: 10px 10px 10px 10px;
    background-color: #1A1B21 ;
    margin-top: 20px;

}

.profile{
    width: 100%;
}

.profileDetails{
    /* background-color: #1A1B21 ; */
}

.profileName{
    display: flex;
    justify-content: center;
    color: #FFFF;
    /* padding: 21px 70px; */
    padding-top: 21px ;
    font-size: 25px;
}
.profileRoll{
    display: flex;
    justify-content: center;
    font-size: 13px;
    font-weight: 400;
    color: #F3BF99 ;
}
.profileWebsite{
    display: flex;
    justify-content: center;
    color: #F5F5F5;
    padding-top: 8px;
    font-size: 10px;

}

.social--btn{
    display: flex;
    padding: 0px 40px;
    padding-top: 15px;
    justify-content: space-between;


}
.profileEmail{
    background-color: #FFFF;
    width: 115px;
    height: 34px;
    /* padding: 9px 13px 9px 11px; */
    border-radius: 6px;
    gap: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px ;
    font-weight: 500;
}
.blue--btn{
    background-color: #5093E2;
    color: #FFFF;
}
.profileEmail>img{
    width: 16px;
}

.about--heading{
    color:#F5F5F5F5;
    padding-top: 15px;
}
.about--para{
    color: #DCDCDC;
    font-size: 10px;
    line-height: 15px;
}
.about{
    padding: 0px 40px;
    padding-top: 15px;
}

.social{
    display: flex;
    justify-content: space-between;
    padding:20px 70px;
    margin-top: 40px;
    background-color: #161619;
    border-radius: 0px 0px 10px 10px;
}
.social--img{
    width: 25px;
}